import React from 'react'
import {Link} from 'gatsby'
import {css} from '@emotion/core'
import styled from '@emotion/styled'

import './index.css';
import {tags as tagList} from './../../constants'

const ReadLink = styled(Link)`
    display:block;
    text-decoration:none;

`

class SearchbarComponent extends React.Component{
    constructor(props){
        super(props)
        this.state={
            text : "",
            renderData : [],
            searchResultsCount : 0
        }
    }
    componentDidMount(){
        console.log("in componentDidMount")
        const allNodes = this.props.allNodes
        console.log("allNodes ",allNodes)
        this.setState({
            renderData: allNodes,
            searchResultsCount : allNodes.length
        })
    }
    searchText = (searchText)=>{
        console.log("searchText ",searchText)  
        let filteredData = this.props.allNodes.filter((data,index)=>(searchText.length >=3
            ? data.searchstring.toLowerCase().includes(searchText.toLowerCase()) ? true : false
            : true
        ))

        this.setState({
            renderData : filteredData,
            text:searchText,
            searchResultsCount : filteredData.length
        })
        
    }
    filterByTag = (selectedtag)=>{
        console.log("selectedtag ",selectedtag)
        let filteredData = this.props.allNodes.filter((data,index)=>(
            data.tags.includes(selectedtag) ? true : false            
        ))
        this.setState({
            renderData : filteredData,
            text: '['+selectedtag+']',
            searchResultsCount : filteredData.length
        })
    }
    render(){
        
        return(
            <div className="searchcontainer">
                <div 
                css={css`
                    position: relative;
                    top: -6px;
                    text-align:center;
                    margin-bottom:10px;
                    font-size:13px;
                    .tag{
                        border-radius:2px;
                        border:0px solid grey;
                        background-color:#e1ecf4;
                        color:#38739d;
                        padding:3px 5px;
                    }
                    .tag:hover{
                        cursor:pointer;
                        background-color:#cee0ed;
                    }
                    .tag:not(:first-of-type){
                        margin-left:5px;
                    }
                `}
                >
                    {
                        tagList.map((data,index)=>(
                            <span className="tag" key={index} 
                                onClick={()=>this.filterByTag(data)}>{data}</span>
                            )
                        )
                    }
                </div>
                <div className="searchbar">
                    <input 
                        placeholder="Search for articles (title/tag)"
                        type="text" 
                        value={this.state.text} 
                        onChange={(event) => this.searchText(event.target.value)} />
                    <div className="searchemoji">üîç</div>
                </div>
                <div css={css`
                    position:relative;
                    top:5px;
                    //left:3px;
                    display: flex;
                    justify-content: space-between;

                    .resultcount{
                        font-size:12px;
                        position:relative;
                        display:inline-block;
                    }
                    .clearsearch{
                        display:inline-block;
                        font-size:12px;
                        border-bottom:1px solid #928f8f;
                        cursor:pointer;
                        position: relative;
                        color:#928f8f;
                        &:hover{
                            color:#b3afaf;
                            border-bottom:1px solid #b3afaf;
                        }
                    }
                `}>
                    <div className="resultcount">
                        {this.state.searchResultsCount} {this.state.searchResultsCount === 1 ? 'result' : 'results'}
                    </div>
                    <div className="clearsearch" 
                        onClick={()=>this.searchText("")}>clear search</div>
                    
                </div>
                <div className="searchresults" css={css`{
                    .result{
                        border-bottom:1px solid #dcd4d4;
                        padding: 10px 0px;
                        .title{
                            color:#0276cc;
                        }
                        .title:hover{
                            color:#56aff1;
                            cursor:pointer;
                        }
                        .meta{
                            padding: 3px 0px;
                            font-size:11px;
                            .publisheddate{
                                display:inline-block;
                                color:#757575;
                            }
                            .tags{
                                display:inline-block;
                                margin-top:3px;
                                margin-left:15px;
                                .tag{
                                    display:inline-block;
                                    padding:1.2px 4.5px;
                                    border-radius:2px;
                                    border:0px solid grey;
                                    background-color:#e1ecf4;
                                    color:#38739d;
                                }
                                .tag:hover{
                                    background-color:#cee0ed;
                                    cursor:default;
                                }
                                .tag:not(:first-of-type){
                                    margin-left:4px;
                                }
                            }
                        }
                        
                    }
                }`}>
                    {
                        this.state.renderData.map((data,index)=>{
                            let tagData = null
                            let tagsplit = data.tags.split(",")
                            if(tagsplit.length > 1){
                                tagData = tagsplit.map((data,index)=>(
                                    <div key={index} className="tag">{data}</div>
                                ))
                            }
                            else{
                                tagData = <div className="tag">{tagsplit}</div>
                            }
                            return (
                                <div key={index} className="result">
                                    <ReadLink className="title" to={data.slug}>{data.title}</ReadLink>
                                    <div className="meta">
                                        <div className="publisheddate">{data.publisheddate}</div>
                                        <div className="tags">
                                            {tagData}
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}

export default SearchbarComponent