import {graphql,useStaticQuery} from 'gatsby'

const useAllPosts=()=>{

    const data = useStaticQuery(graphql`
        query {
            allMdx {
                nodes {
                    frontmatter {
                        author
                        slug
                        title
                        articleid
                        publisheddate
                        tags
                    }
                }
            }
        }
    `)
    return data.allMdx.nodes
}

export default useAllPosts;